$ns: "fdt-";
@import 'style/global.scss';

.#{$ns}CartList {
    &__container {
        position: relative;

        width: 832px;

        overflow: hidden;
    }

    &__wrapper {
        overflow-y: scroll;

        width: 832px;
        padding-right: 10px;

        --spatial-navigation-contain: contain;

        max-height: 540px;
    }

    &__total-price {
        margin-top: 56px;
        line-height: 56px;
        font-size: 48px;
        font-weight: 600;
        color: #fff;
    }

    &__gradient {
        position: absolute;
        left: 0;
        right: 0;

        height: 160px;

        z-index: 1;

        background: linear-gradient(0, #001124 0%, rgba(0, 17, 36, 0) 89.09%);
        //background: linear-gradient(0, red 0%, rgba(0, 17, 36, 0) 89.09%);

        &_top {
            top: 0;

            transform: rotate(180deg);
        }

        &_bottom {
            bottom: 0;
        }

        &_hidden {
            opacity: 0;
        }
    }
}
