$ns: "fdt-";
@import 'style/global.scss';

.#{$ns}CatalogListItem {
    position: relative;
    width: 392px;
    height: 592px;

    margin-right: 32px;

    display: flex;
    flex-direction: column;

    overflow: hidden;
    border-radius: 24px;
    box-shadow: 0 16px 48px rgba(0, 0, 0, 0.1);

    transition: .4s ease-in-out;
    will-change: transform;
    will-change: background-color;

    &:focus {
        transform: scale(1.08);

        background-color: rgba(255, 255, 255, .1);

        .#{$ns}CatalogListItem {
            &__add-badge {
                opacity: 1;

                &::before {
                    background-image: url('../../../assets/icons/plus.svg');
                }
            }
        }
    }

    &__index-badge {
        position: absolute;
        left: 16px;
        top: 16px;

        height: 64px;
        width: 64px;
        text-align: center;
        line-height: 64px;
        font-size: 28px;
        font-weight: 600;
        color: rgba(0, 0, 0, 0.55);

        background: #EAEAEC;
        border-radius: 40px;
    }

    &__thumb {
        display: block;
        height: 392px;
        width: 392px;

        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }

    &__description {
        position: relative;

        padding: 32px 32px 36px;
        box-sizing: border-box;

        height: 200px;
        background: rgba(255, 255, 255, 0.05);
    }

    &__add-badge {
        position: absolute;
        top: -52px;
        right: 24px;

        display: flex;
        justify-content: center;
        align-items: center;

        width: 72px;
        height: 72px;

        opacity: 0;
        border-radius: 50%;
        background: #08A652;

        transition: opacity .2s ease-in-out;

        .#{$ns}CatalogListItem &_done {
            opacity: 1;
        }

        &::before {
            content: '';

            display: block;

            height: 32px;
            width: 32px;

            background-image: url('../../../assets/icons/done.svg');
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
        }
    }

    &__description-name {
        height: 80px;
        line-height: 40px;
        font-size: 32px;
        font-weight: 500;
        color: #fff;

        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
    }

    &__price-container {
        margin-top: 12px;
        display: flex;
    }

    &__price {
        height: 40px;
        line-height: 40px;

        font-size: 32px;
        font-weight: 600;
        color: #fff;
    }

    &__count {
        height: 40px;
        line-height: 40px;

        margin-left: 8px;

        font-size: 32px;
        font-weight: 600;
        color: #08A652;
    }
}

